<template>
  <div>
    <h1>{{em}}    .</h1>
    <div>     </div>

  </div>
</template>

<script>
export default {

  data () {
    return {
      em : '',
    }
  },
  created () {
    let query = window.location.search;
    let param = new URLSearchParams(query);
    let em = param.get('em');
    this.em = em;
    var data = {
      em : em
    }
    this.$store.dispatch("sendEmail",data);
    },
}
</script>

